import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
var app = getApp().globalData
export default {
	data() {
		return {
			show: false,
			vote_status: false,
			tel: '',
			like: 0,
			id: '',
			Detail: '',
		}
	},
	onShareAppMessage:function(res) {
		return {
		  title: this.Detail.title?this.Detail.title.substring(0,30)+'...':'德申汇项目详情',
		  imageUrl:this.Detail.images[0].url_image?this.Detail.images[0].url_image:app.shareImg,
		  path: '/pagesMy/InformationDetail/InformationDetail?id='+this.id
		}

	},
	onLoad(e) {
		this.id = e.id
		this.getData()
	},
	methods: {
		MyInfo(id){
			if(uni.getStorageSync('userId')){
				uni.navigateTo({
					url:'../../pagesMy/MyInfo/MyInfo?id='+id
				})
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
			
		},
		// 关注
		vote () {
		      if (uni.getStorageSync("userId")) {
					if (this.vote_status) {
					  this.vote_status = false
					  this.like--
					} else {
					  this.vote_status = true
					  this.like++
					}
		        this.$api.Opportunity_vote({
		          user_id: uni.getStorageSync("userId"),
		          token: uni.getStorageSync("token"),
		          id: this.id
		        }).then(res => {
		          if (res.data.code === '1039') {
		            uni.showToast({
		                title: '取消收藏',
		                duration: 2000,
		            	icon:'none'
		            });
		          } else if (res.data.code === '1038') {
		            
					uni.showToast({
					    title: '收藏成功',
					    duration: 2000,
						icon:'none'
					});
		          }
		        })
		      } else {
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
		      }
		},
		// 电话咨询
		phone () {
			if(uni.getStorageSync('userId')){
				if (uni.getStorageSync("isVip") == 0) {
						Dialog.confirm({
						  title: '提示',
						  message: '您还不是VIP用户',
						  confirmButtonText:'去支付'
						}).then(() => {
						  // on confirm
						}).catch(() => {
						  // on cancel
						});
				} else if (uni.getStorageSync("isVip") == 1) {
						Dialog.confirm({
						  title: '电话',
						  message: '当前商家电话为' + this.tel + '，是否立即呼叫？',
						  confirmButtonText:'我要拨打'
						}).then(() => {
							uni.makePhoneCall({
								phoneNumber: this.tel
							});
						}).catch(() => {
						  // on cancel
						});
										
				}
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
		     
		},
		getData(){
			this.$api.Opportunity_detail({
				token: uni.getStorageSync("token")|| '',
				user_id: uni.getStorageSync("userId") || '',
				id:this.id
			}).then(res=>{
				if (res.data.code === '200') {
				  this.Detail = res.data.data
				  this.vote_status = res.data.data.vote_status
				  this.like = res.data.data.like
				  this.tel = res.data.data.username
				} else if (res.data.code === '1010') {
				  uni.clearStorageSync()
				  uni.showToast({
				      title: '登录过期，请重新登录',
				      duration: 2000,
				  	  icon:'none'
				  });
				  uni.reLaunch({
				  	url: '../../pages/login/login'
				  })
				}
			})
		}
	}
}
